}

.dashboard-container {
  padding: 20px;
  text-align: left;
}

.dashboard-heading {
  font-size: 28px;
  color: var(--primary-color);
  margin-bottom: 20px;
  
}

/* Button Row */
.dashboard-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
}

/* Custom button styles for Dashboard */
.credit-btn {
  background-color:#ACD3A8;
  color: white;
}

.credit-btn:hover {
  background-color:#bcdab9;
}
.debit-btn:hover{
  background-color: #bd5151;
}
.debit-btn {
  background-color: #BF3131;
  color: white;
}

.view-all-btn {
  background-color: var(--tertiary-color);
}

/* Ensure icon spacing */
.icon {
  margin-right: 8px;
}

.input-styles{
  display: block;
  margin-top: 20px;
}

.stats-container {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  gap: 20px;
}

.stats-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 300px;
  padding: 20px;
  background: linear-gradient(135deg, rgba(143, 135, 241, 0.9), rgba(198, 142, 253, 0.9));
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  color: white;
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

/* Hover effect */
.stats-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

/* Left section for icon */
.stats-card .icon-wrapper {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.3);
}

/* Icons */
.stats-card .icon {
  font-size: 25px;
  color: white;
}

/* Right section */
.stats-card .content {
  flex: 1;
  text-align: right;
  padding-left: 15px;
}

.stats-card .title {
  font-size: 16px;
  font-weight: bold;
  opacity: 0.9;
}

.stats-card .amount {
  font-size: 22px;
  font-weight: bold;
}

/* Background glow effect */
.stats-card::before {
  content: "";
  position: absolute;
  width: 120px;
  height: 120px;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  top: -20px;
  left: -20px;
  filter: blur(30px);
}


/* Green color for credit (up arrow) */
.stats-card .icon.up {
color: #4CAF50; /* Green */
}

/* Red color for debit (down arrow) */
.stats-card .icon.down {
color: #E74C3C; /* Red */
}

