/* ========== Mobile Adjustments ========== */
@media (max-width: 768px) {
  
  /* Center and Stack Dashboard Buttons */
  .dashboard-buttons {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .dashboard-buttons button {
    width: 100%;
    max-width: 300px;
  }

  /* Responsive Cards */
  .card {
    width: 100%;
    min-width: auto;
    margin-bottom: 10px;
    padding: 15px;
  }

  /* Stack Stats Cards */
  .stats-container {
    flex-direction: column;
    align-items: center;
  }

  .stats-card {
    width: 100%;
  }

  /* Responsive Transactions Table */
  .transactions-table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  .transactions-table th,
  .transactions-table td {
    font-size: 12px;
    padding: 8px;
  }

  /* Responsive Modals */
  .modal {
    width: 95%;
    padding: 15px;
  }

  /* Responsive Graph */
  .graph-container {
    width: 100%;
    max-width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-x: auto;
  }
  .display-flex{
    display: block;
  }
}
